.game {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px;
  font-family: 'Segoe UI', system-ui, sans-serif;
  max-width: 400px;
  margin: 40px auto;
  padding: 20px;
  background: linear-gradient(145deg, #ffffff, #f0f0f0);
  border-radius: 20px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
}

.status {
  margin-bottom: 20px;
  font-size: 28px;
  font-weight: 600;
  color: #333;
  text-align: center;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
}

.board {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 8px;
  background-color: #2c3e50;
  padding: 8px;
  border-radius: 12px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.square {
  width: 90px;
  height: 90px;
  background-color: #ecf0f1;
  border: none;
  border-radius: 8px;
  font-size: 32px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #2c3e50;
}

.square:disabled {
  cursor: not-allowed;
  opacity: 0.9;
}

.square:hover:not(:disabled) {
  background-color: #fff;
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.square[data-player="X"] {
  color: #e74c3c;
}

.square[data-player="O"] {
  color: #3498db;
}

.reset-button {
  margin-top: 30px;
  padding: 12px 28px;
  font-size: 18px;
  font-weight: 600;
  background-color: #2ecc71;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 1px;
  box-shadow: 0 4px 6px rgba(46, 204, 113, 0.2);
}

.reset-button:hover {
  background-color: #27ae60;
  transform: translateY(-2px);
  box-shadow: 0 6px 12px rgba(46, 204, 113, 0.3);
}

.reset-button:active {
  transform: translateY(1px);
}

/* Winning animation */
@keyframes winner {
  0% { transform: scale(1); }
  50% { transform: scale(1.1); }
  100% { transform: scale(1); }
}

.square.winner {
  animation: winner 0.6s ease infinite;
  background-color: #f1c40f;
  color: #fff;
}

/* Game over overlay */
.game-over {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 48px;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s ease;
}

.game-over.active {
  opacity: 1;
  pointer-events: all;
}

/* Responsive design */
@media (max-width: 480px) {
  .game {
    margin: 20px 10px;
    padding: 15px;
  }

  .square {
    width: 70px;
    height: 70px;
    font-size: 28px;
  }

  .status {
    font-size: 24px;
  }

  .reset-button {
    padding: 10px 20px;
    font-size: 16px;
  }
}